<template>
    <div id="treeFrame" class="treePane">
        <div id="framesTree"></div>
    </div>
</template>

<script>
    module.exports = {
        props: ['url'],
        data() {
            return {}
        },
        computed: {},
        created() {
        },
        methods: {},
        mounted: function () {
            //let that = this;
            window.$('#framesTree').tree({
                url: this.url,
                onClick: (node) => {
                    console.log(node);
                    if (node.id.charAt(0) == 'f') {
                        this.$store.dispatch('setActionNode', {'action': 'showframe', node: node})
                    }
                    if (node.id.charAt(0) == 'l') {
                        //that.showLU(node.id.substr(1));
                    }
                },
                onLoadSuccess: (node, data) => {
                    console.log(data.length);
                    this.$store.commit('treeFrameCount', data.length);
                }
            });
            this.$store.watch(
                (state, getters) => getters.searchFrame,
                (searchFrame) => {
                    console.log(searchFrame);
                    window.$('#framesTree').tree({queryParams: searchFrame});
                },
                {
                    deep: true
                }
            )

        },
        watch: {}
    }
</script>

<style scoped>
    .treePane {
        padding: 1rem;
    }
</style>
