<template>
    <div>
        <div class="title">
            <div class="ui right floated icon main menu">
                <a class="item">
                    <i class="edit icon"></i>
                </a>
                <a class="item">
                    <i class="expand icon"></i>
                </a>
            </div>
            <h2 class="ui header">{{name}}</h2>
            <span class="ui text" v-html="description"></span>
            <!--
            <report-frame-domains :domains="domains"></report-frame-domains>
            -->
        </div>
        <div>
            <!--
              <report-frame-examples v-if="examples"></report-frame-examples>
              <report-frame-fecore></report-frame-fecore>
              <report-frame-fenoncore></report-frame-fenoncore>
              <report-frame-relations></report-frame-relations>
              <report-frame-lus></report-frame-lus>
              -->
        </div>
    </div>
</template>

<script>
    module.exports = {
        components: {
            //'report-frame-domains': import('@/components/report/frame/domains'),
            //'report-frame-examples': window.httpVueLoader('report/frame/examples'),
            //'report-frame-fecore': window.httpVueLoader('report/frame/fecore'),
            //'report-frame-fenoncore': window.httpVueLoader('report/frame/fenoncore'),
            //'report-frame-relations': window.httpVueLoader('report/frame/relations'),
            //'report-frame-lus': window.httpVueLoader('report/frame/lus')
        },
        props: ['id'],
        data() {
            return {
                name: '',
                description: '',
                isMaster: true,
                domains: []
            }
        },
        mounted() {
            console.log('showFrame mounted id = ' + this.id);

            let url = window.manager.getApiUrl('frame data', {id: this.id});
            console.log(url);
            window.$.get(url)
                .done(data => {
                    console.log(data);
                    this.name = data.frame.entry.name;
                    this.description = data.frame.entry.description;
                    this.domains = data.domains;
                });

        },
        methods: {
        }
    }
</script>
<style type="text/css" scoped>
    #reportFrame > .title {
        display: block;
    }

    .main.menu {
        margin-top: -2px;
        min-height: 0em;
    }

    .main.menu > .item {
        padding-top: 0.785714em;
        padding-bottom: 0.785714em;
    }

    .fe_target {
        color: rgb(255, 255, 255);
        background-color: rgb(0, 0, 0);
        text-transform: uppercase;
    }

    /*
        @foreach ($data->fe['styles'] as $style)
        .fe_ {
            ! ! $ style [ 'fe' ] ! !
        } {
            color: #{{$style['rgbFg']}
        }
        ;
        background-color: #{{$style['rgbBg']}

        }
        ;
        }
        @endforeach
    */
</style>
