import vueCustomElement from 'vue-custom-element'

Vue.use(vueCustomElement)
StructureFrameControl.store = store;
StructureFrameTree.store = store;
StructureFrameContent.store = store;
/*
Vue.customElement('structure-frame-control', StructureFrameControl)
Vue.customElement('structure-frame-tree', StructureFrameTree)
Vue.customElement('structure-frame-content', StructureFrameContent)
 */
Vue.customElement('structure-frame-tree', () => new Promise((resolve) => {
    require(['./components/structure/frame/tree'], (lazyComponent) => resolve(lazyComponent.default));
}),{props: ['url']});
Vue.customElement('structure-frame-control', () => new Promise((resolve) => {
    require(['./components/structure/frame/control'], (lazyComponent) => resolve(lazyComponent.default));
}));
Vue.customElement('structure-frame-content', () => new Promise((resolve) => {
    require(['./components/structure/frame/content'], (lazyComponent) => resolve(lazyComponent.default));
}));
Vue.customElement('report-frame-show', () => new Promise((resolve) => {
    require(['./components/report/frame/show'], (lazyComponent) => resolve(lazyComponent.default));
}));