<template>
    <div id="controlFrame" class="controlPane">
        <form class="ui form">
            <div class="ui horizontal divider">
                Search
            </div>
            <div class="field">
                <label>Domain</label>
                <div id="domain" class="ui selection dropdown">
                    <input type="hidden" id="idDomain" name="domain" @change="onChangeDomain">
                    <i class="dropdown icon"></i>
                    <div class="default text">Domain</div>
                </div>
            </div>
            <div class="field">
                <label>Frame</label>
                <div class=" ui right icon input">
                    <input id="frame" name="frame" type="text" v-model="params.frame">
                    <i class="times link icon" @click="onClear('frame')"></i>
                </div>
            </div>
            <div class="field">
                <label>Frame Element</label>
                <div class=" ui right icon input">
                    <input id="fe" name="fe" type="text" v-model="params.fe">
                    <i class="times link icon" @click="onClear('fe')"></i>
                </div>
            </div>
            <div class="field">
                <label>LU</label>
                <div class=" ui right icon input">
                    <input id="lu" name="lu" type="text" v-model="params.lu">
                    <i class="times link icon" @click="onClear('lu')"></i>
                </div>
            </div>
            <!--
            <div class="field">
                <div class="ui button primary fluid" tabindex="0" @click="onClickSearch">Search</div>
            </div>
            -->
        </form>
        <div class="ui horizontal divider">
            Action
        </div>
        <div class="ui button secondary fluid" tabindex="0">New Frame</div>
        <div class="ui horizontal divider">
            Stats
        </div>
        <a class="ui label">
            #Frames
            <div class="detail">{{ frameCount }}</div>
        </a>
    </div>
</template>


<script>
    module.exports = {
        props: [],
        data() {
            return {
                frameCount: 0,
            }
        },
        computed: {
            params: {
                get() {
                    return this.$store.state.searchFrame
                },
                set(value) {
                    this.$store.dispatch('searchFrame', value);
                }
            }
        },
        created() {
        },
        methods: {
            onChangeDomain(e) {
                this.params.idDomain = e.target.value;
            },
            onClear(name) {
                this.params[name] = '';
            }
        },
        mounted: function () {
            let url = window.manager.getApiUrl('domain list');
            console.log(url);
            window.$.get(url)
                .done(data => {
                    console.log(data);
                    let response = [];
                    window.$.each(data, function (index, item) {
                        response.push({
                            name: item.name,
                            value: item.idDomain
                        })
                    });
                    window.$('#domain')
                        .dropdown({
                            values: response
                        })
                });
            this.$store.watch(
                (state, getters) => getters.treeFrameCount,
                (count) => {
                    console.log(count);
                    this.frameCount = count;
                },
                {
                    deep: true
                }
            )

        },
        watch: {}
    }
</script>

<style scoped>
    .controlPane {
        padding: 1rem;
    }
</style>

