<template>
    <div id="contentFrame" class="contentPane">

        <component :is="currentComponent" :key="id" :id="id"></component>
        <!--
        <report-frame-show :key="id" :id="id"></report-frame-show>
        -->
    </div>
</template>

<script>
    import ReportFrameShow from '@/components/report/frame/show.vue'

    export default {
        components: {
            ReportFrameShow
        },
        props: [],
        data() {
            return {
                id: 0,
                currentComponent: '',
            }
        },
        computed: {},
        created() {
        },
        methods: {
            getidFromNode(node) {
                return node.id.substr(1);
            }
        },
        mounted: function () {
            this.$store.watch(
                (state, getters) => getters.actionNode,
                (actionNode) => {
                    this.id = this.getidFromNode(actionNode.node);
                    this.currentComponent = 'report-frame-show';
                    console.log(this.currentComponent);
                }
            )
        },
        watch: {}
    }
</script>

<style scoped>
    .contentPane {
        padding: 1rem;
    }
</style>

