import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentState: 'loading', // playing, paused, loading, loaded
    currentNode: {},
    actionNode: {action: '', node: 0},
    searchFrame: {
      idDomain: 1,
      frame: '',
      fe: '',
      lu: ''
    },
    treeFrameCount: 0,
  },
  getters: {
    currentState(state) {
      return state.currentState
    },
    currentNode(state) {
      return state.currentNode
    },
    actionNode(state) {
      return state.actionNode
    },
    searchFrame(state) {
      return state.searchFrame
    },
    treeFrameCount(state) {
      return state.treeFrameCount
    },
  },
  mutations: {
    currentState(state, value) {
      state.currentState = value;
    },
    searchFrame(state, value) {
      state.searchFrame = value;
    },
    treeFrameCount(state, value) {
      state.treeFrameCount = value;
    },
    currentNode(state, value) {
      state.currentNode = value;
    },
    actionNode(state, value) {
      state.actionNode = value;
    },
  },
  actions: {
    setActionNode(context, actionNode) {
      context.commit('actionNode', actionNode);
      context.commit('currentNode', actionNode.node);
    },
    searchFrame(context, params) {
      console.log(params);
      context.commit('searchFrame', params);
    }
  },
})
